<template>
  <div class="slider">
    <swiper
      :slidesPerView="1"
      :navigation="true"
      :breakpoints="{
        '768': {
          slidesPerView: 2,
        },
        '1024': {
          slidesPerView: 4,
        },
        '1920': {
          slidesPerView: 5,
        },
      }"
      :loop="true"
      :loopFillGroupWithBlank="true"
      :speed="5000"
      :autoplay="{
        enabled: true,
        delay: 1,
        disableOnInteraction: false,
      }"
      :modules="modules"
      class="mySlider"
    >
      <swiper-slide
        class="mySlider__slide"
        v-for="(item, index) in sliderItem"
        :key="index"
        ><img
          class="mySlider__slide-img"
          :src="require('@/assets/images/slider/' + item.img)"
      /></swiper-slide>
    </swiper>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from "swiper/vue";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import { Navigation, Autoplay } from "swiper";
export default {
  data() {
    return {
      sliderItem: [
        {
          img: "1.png",
        },
        {
          img: "2.png",
        },
        {
          img: "3.png",
        },
        {
          img: "4.png",
        },
        {
          img: "5.png",
        },
        {
          img: "6.png",
        },
        {
          img: "7.png",
        },
        {
          img: "8.png",
        },
        {
          img: "9.png",
        },
        {
          img: "10.png",
        },
      ],
    };
  },
  components: {
    Swiper,
    SwiperSlide,
  },
  setup() {
    return {
      modules: [Navigation, Autoplay],
    };
  },
};
</script>

<style lang="scss">
@import "@/assets/scss/grid/grid";
.slider {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
  height: 100%;
  .mySlider {
    width: 100%;
    @include adaptivHeightlg(550, 375);
    overflow: hidden;
    @media (max-width: 768px) {
      @include adaptivHeightmd(600, 455);
    }
    &__slide {
     width: 100%;
      height: 100%;
      overflow: hidden;
      @media(max-width: 768px){
        max-width: 100%;
      }
      &-img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        transform: scale(1);
        transition: 0.5s;
        &:hover {
          transform: scale(1.05);
          transition: 0.5s;
          @media (max-width: 768px) {
            transform: none;
          }
        }
        @media (max-width: 768px) {
          width: 100%;
        }
      }
    }

    &-slide {
      overflow: hidden;
    }
    .swiper-button-next,
    .swiper-button-prev {
      display: none;
      // top: 95%;
      // @include adaptivWidthLg(112, 90);
      // width: 100%;
      // @include adaptivHeightlg(23, 20);
      // @media (max-width: 768px) {
      //   transform: translate(0%, 50%);
      //   @include adaptivWidthmd(130, 70);
      //   width: 100%;
      //   @include adaptivHeightmd(28, 15);
      // }
      // &::after {
      //   background-image: url("../assets/images/icon/right.png");
      //   background-size: cover;
      //   display: inline-block;
      //   width: 100%;
      //   height: 100%;
      //   content: "";
      // }
    }
    // .swiper-button-prev {
    //   &::after {
    //     background-image: url("../assets/images/icon/left.png");
    //     background-size: cover;
    //     display: inline-block;
    //     width: 100%;
    //     height: 100%;
    //     content: "";
    //   }
    // }
  }
}
</style>